<template>
  <div>
    <v-row class="mx-8 mb-8">
      <v-col cols="12" v-if="allUnapprovedUsers.length > 0">
        <div class="text-h4 text-center blue--text font-weight-bold my-6">
          
        </div>
      </v-col>

      <v-col v-for="(item, index) in allUnapprovedUsers" :key="index" cols="2">
        <v-card outlined rounded="xl">
          <div class="text-center py-8">
            <v-avatar v-if="(index + 1) % 2 === 1" size="164">
              <img src="../assets/avatar1.png" alt="XDP" />
            </v-avatar>
            <v-avatar v-else size="164">
              <img src="../assets/avatar2.png" alt="XDP" />
            </v-avatar>
          </div>
          <div>
            <div class="py-3 mx-4">
              <v-icon class> mdi-account </v-icon>
              <span class="text-caption ms-3">{{ item.username }}</span>
            </div>
            <v-divider></v-divider>
            <div class="py-3 mx-4">
              <v-icon class> mdi-eye </v-icon>
              <span class="text-caption ms-3">******</span>
            </div>
            <v-divider></v-divider>
            <div class="py-3 mx-4">
              <v-icon>mdi-identifier</v-icon>
              <span class="text-caption ms-3">{{ item.workId }}</span>
            </div>
            <v-divider></v-divider>
            <div class="py-3 mx-4">
              <v-select
                filled
                rounded
                hide-details
                prefix=""
                :items="allRating"
                item-text="ratingName"
                item-value="ratingValue"
                v-model="item.rating"
              ></v-select>
            </div>
            <v-divider></v-divider>
          </div>
          <div class="mx-4 py-3">
            <v-btn rounded block color="primary" @click="approveOK(item)">
              
            </v-btn>
          </div>
        </v-card>
      </v-col>

      <v-col cols="12">
        <div class="text-h4 text-center blue--text font-weight-bold mt-16">
          
        </div>
      </v-col>

      <v-col cols="12">
        <v-row>
          <v-col cols="4" offset="4">
            <v-text-field
              filled
              rounded
              label=""
              append-icon="mdi-magnify"
              v-model="searchName"
            ></v-text-field>
          </v-col>
        </v-row>
      </v-col>

      <v-col v-for="(item, index) in allApprovedUsers" :key="index" cols="2">
        <v-card rounded="xl" outlined>
          <div class="text-center py-8">
            <v-avatar v-if="(index + 1) % 2 === 1" size="164">
              <img src="../assets/avatar1.png" alt="XDP" />
            </v-avatar>
            <v-avatar v-else size="164">
              <img src="../assets/avatar2.png" alt="XDP" />
            </v-avatar>
          </div>
          <div>
            <div class="py-3 mx-4">
              <v-icon class> mdi-account </v-icon>
              <span class="text-caption ms-3">{{ item.username }}</span>
            </div>
            <v-divider></v-divider>
            <div class="py-3 mx-4">
              <v-icon>mdi-identifier</v-icon>
              <span class="text-caption ms-3">{{ item.workId }}</span>
            </div>
            <v-divider></v-divider>
            <div class="py-3 mx-4">
              <v-select
                filled
                rounded
                hide-details
                prefix=""
                :items="allRating"
                item-text="ratingName"
                item-value="ratingValue"
                v-model="item.rating"
              ></v-select>
            </div>
            <v-divider></v-divider>
          </div>
          <div class="mx-4 py-3">
            <v-btn rounded block color="primary" @click="updateRating(item)">
              
            </v-btn>
          </div>
        </v-card>
      </v-col>
    </v-row>

    <Snackbar ref="snackbar"></Snackbar>
  </div>
</template>

<script>
import Snackbar from "../components/Snackbar.vue";
export default {
  components: { Snackbar },
  data: () => ({
    // 
    searchName: "",
    // 
    allUnapprovedUsers: [],
    // 
    allApprovedUsers: [],
    // 
    tempAllApprovedUsers: [],
    // 
    allRating: [
      { ratingName: "", ratingValue: 1 },
      { ratingName: "/", ratingValue: 2 },
      { ratingName: "//", ratingValue: 2.5 },
      { ratingName: "//", ratingValue: 3 },
      { ratingName: "///", ratingValue: 4 },
      { ratingName: "////", ratingValue: 5 },
    ],
  }),

  async created() {
    this.allUnapprovedUsers = (
      await this.$axios({
        method: "get",
        url: "/userGetAllUnapproved",
      })
    ).data;
    this.allApprovedUsers = (
      await this.$axios({
        method: "get",
        url: "/userGetAllApproved",
      })
    ).data;
    this.tempAllApprovedUsers = JSON.parse(
      JSON.stringify(this.allApprovedUsers)
    );
  },

  methods: {
    // 
    async approveOK(item) {
      try {
        // 
        await this.$axios({
          method: "post",
          url: "/userApprovedOK",
          data: {
            _id: item._id,
            rating: item.rating,
          },
        });

        // 
        this.allUnapprovedUsers = (
          await this.$axios({
            method: "get",
            url: "/userGetAllUnapproved",
          })
        ).data;
        this.allApprovedUsers = (
          await this.$axios({
            method: "get",
            url: "/userGetAllApproved",
          })
        ).data;
        this.tempAllApprovedUsers = JSON.parse(
          JSON.stringify(this.allApprovedUsers)
        );

        this.$refs.snackbar.snackbarStatus = "info";
        this.$refs.snackbar.snackbarText = "";
        this.$refs.snackbar.snackbarShow = true;
      } catch (error) {
        console.log(error);
      }
    },

    // 
    async updateRating(item) {
      try {
        // 
        await this.$axios({
          method: "post",
          url: "/userUpdateRating",
          data: {
            _id: item._id,
            rating: item.rating,
          },
        });

        // 
        this.allApprovedUsers = (
          await this.$axios({
            method: "get",
            url: "/userGetAllApproved",
          })
        ).data;
        this.tempAllApprovedUsers = JSON.parse(
          JSON.stringify(this.allApprovedUsers)
        );
        this.$refs.snackbar.snackbarStatus = "info";
        this.$refs.snackbar.snackbarText = "";
        this.$refs.snackbar.snackbarShow = true;
      } catch (error) {
        console.log(error);
      }
    },
  },
  watch: {
    searchName() {
      // allUsers
      this.allApprovedUsers = JSON.parse(
        JSON.stringify(this.tempAllApprovedUsers)
      );
      const result = [];
      this.allApprovedUsers.forEach((item, index, arr) => {
        if (item.username.includes(this.searchName)) {
          result.push(item);
        }
      });
      this.allApprovedUsers = JSON.parse(JSON.stringify(result));
    },
  },
};
</script>
